
.blog-section {
    display: flex;
    padding: var(--spacing-9xl, 6rem) var(--spacing-none, 0rem);
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-7xl, 4rem);
    align-self: stretch;
    background: var(--Colors-Background-bg-primary, #FFF);
    box-sizing: border-box;


    .extend-blog {
        display: flex;
        // max-width: var(--container-max-width-desktop, 80rem);
        // padding: var(--spacing-none, 0rem) var(--container-padding-desktop, 2rem);
        flex-direction: column;
        align-items: flex-start;
        gap: var(--spacing-4xl, 2rem);
        // align-self: stretch;
    }

    .blog-section-container-top {
        // width: $--container-max-width-desktop;
        max-width: 75rem;
        width: 100%;

        .blog-section-content {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            align-content: flex-start;
            row-gap: var(--spacing-4xl, 2rem);
            align-self: stretch;
            flex-wrap: wrap;

            .blog-heading-n-supporting-text {
                display: flex;
                min-width: var(--width-sm, 30rem);
                max-width: var(--width-xl, 48rem);
                flex-direction: column;
                align-items: flex-start;
                gap: var(--spacing-2xl, 1.25rem);
                flex: 1 0 0;

                .blog-heading {
                    color: var(--colors-text-text-primary-900, #101828);

                    /* Display md/Semibold */
                    font-family: var(--Font-family-font-family-display, Inter);
                    font-size: var(--Font-size-display-md, 2.25rem);
                    font-style: normal;
                    font-weight: 600;
                    line-height: var(--Line-height-display-md, 2.75rem);
                    /* 122.222% */
                    letter-spacing: -0.045rem;
                    align-self: stretch;
                }

                .blog-supporting-text {
                    color: var(--colors-text-text-tertiary-600, #475467);

                    /* Text xl/Regular */
                    font-family: var(--Font-family-font-family-body, Inter);
                    font-size: var(--Font-size-text-xl, 1.25rem);
                    font-style: normal;
                    font-weight: 400;
                    line-height: var(--Line-height-text-xl, 1.875rem);
                    /* 150% */
                }
            }

            .blog-actions {
                display: flex;
                align-items: flex-start;
                gap: var(--spacing-lg, 0.75rem);

                .blog-action-btn {
                    display: flex;
                    padding: var(--spacing-lg, 0.75rem) 1.125rem;
                    justify-content: center;
                    align-items: center;
                    gap: var(--spacing-sm, 0.375rem);

                    border-radius: var(--radius-md, 0.5rem);
                    border: 2px solid rgba(255, 255, 255, 0.12);
                    background: var(--Component-colors-Components-Buttons-Primary-button-primary-bg, #1570EF);

                    /* Shadows/shadow-xs-skeuomorphic */
                    box-shadow: 0px 0px 0px 1px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner-border, rgba(16, 24, 40, 0.18)) inset, 0px -2px 0px 0px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner, rgba(16, 24, 40, 0.05)) inset, 0px 1px 2px 0px var(--Colors-Effects-Shadows-shadow-xs, rgba(16, 24, 40, 0.05));

                    color: var(--Component-colors-Components-Buttons-Primary-button-primary-fg, #FFF);

                    /* Text md/Semibold */
                    font-family: var(--Font-family-font-family-body, Inter);
                    font-size: var(--Font-size-text-md, 1rem);
                    font-style: normal;
                    font-weight: 600;
                    line-height: var(--Line-height-text-md, 1.5rem);
                    /* 150% */


                }
            }
        }

    }

    .blog-section-container-bottom {
        @extend .extend-blog;

        .blog-section-content-bottom {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: var(--spacing-4xl, 2rem);

            .blog-posts {
                display: flex;
                align-items: flex-start;
                gap: var(--spacing-4xl, 2rem);
                // max-width: $--container-max-width-desktop;
                max-width: 100vw;
                padding-left: 22.8rem;
                padding-right: 22.8rem;
                overflow-x: auto;
                scrollbar-width: none;

                .blog-post-card {
                    display: flex;
                    width: 24rem;
                    min-width: var(--width-xxs, 20rem);
                    flex-direction: column;
                    align-items: flex-start;
                    gap: var(--spacing-2xl, 1.25rem);
                    overflow: hidden;
                    box-sizing: border-box;
                    flex: 0 0 auto;

                    .blog-post-card-image {
                        width: 100%;
                        height: 15rem;
                        border-radius: var(--radius-2xl, 1rem);
                        background: url("/assets/blog1.png") lightgray 50% / cover no-repeat;
                        overflow: hidden;
                        box-sizing: border-box;
                    }

                    .blog-post-content {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                        gap: var(--spacing-3xl, 1.5rem);
                        align-self: stretch;

                        .blog-heading-n-text {
                            display: flex;
                            flex-direction: column;
                            align-items: flex-start;
                            gap: var(--spacing-md, 0.5rem);
                            align-self: stretch;

                            .blog-username {
                                color: var(--colors-text-text-brand-secondary-700, #175CD3);

                                /* Text sm/Semibold */
                                font-family: var(--Font-family-font-family-body, Inter);
                                font-size: var(--Font-size-text-sm, 0.875rem);
                                font-style: normal;
                                font-weight: 600;
                                line-height: var(--Line-height-text-sm, 1.25rem);
                                /* 142.857% */
                                align-self: stretch;
                            }

                            .blog-heading-n-icon {
                                display: flex;
                                align-items: flex-start;
                                gap: var(--spacing-xl, 1rem);
                                align-self: stretch;

                                .blog-heading-text {
                                    color: var(--colors-text-text-primary-900, #101828);

                                    /* Display xs/Semibold */
                                    font-family: var(--Font-family-font-family-display, Inter);
                                    font-size: var(--Font-size-display-xs, 1.5rem);
                                    font-style: normal;
                                    font-weight: 600;
                                    line-height: var(--Line-height-display-xs, 2rem);
                                    /* 133.333% */
                                    flex: 1 0 0;
                                }

                                .blog-icon-wrap {
                                    display: flex;
                                    padding-top: var(--spacing-xs, 0.25rem);
                                    flex-direction: column;
                                    align-items: flex-start;

                                    img {
                                        width: 1.5rem;
                                        height: 1.5rem;
                                    }
                                }
                            }

                            .blog-supporting-text {
                                display: -webkit-box;
                                -webkit-box-orient: vertical;
                                -webkit-line-clamp: 3;
                                align-self: stretch;
                                overflow: hidden;
                                color: var(--colors-text-text-tertiary-600, #475467);
                                text-overflow: ellipsis;

                                /* Text md/Regular */
                                font-family: var(--Font-family-font-family-body, Inter);
                                font-size: var(--Font-size-text-md, 1rem);
                                font-style: normal;
                                font-weight: 400;
                                line-height: var(--Line-height-text-md, 1.5rem);
                                /* 150% */
                            }
                        }

                        .blog-categories {
                            display: flex;
                            align-items: flex-start;
                            align-content: flex-start;
                            gap: 0.5rem var(--spacing-md, 0.5rem);
                            align-self: stretch;
                            flex-wrap: wrap;

                            .blog-post-badges {
                                display: flex;
                                padding: var(--spacing-xxs, 0.125rem) 0.625rem;
                                align-items: center;
                                border-radius: var(--radius-full, 624.9375rem);
                                border: 1px solid var(--Component-colors-Utility-Brand-utility-brand-200, #B2DDFF);
                                background: var(--Component-colors-Utility-Brand-utility-brand-50, #EFF8FF);

                                span {
                                    color: var(--Component-colors-Utility-Brand-utility-brand-700, #175CD3);
                                    text-align: center;

                                    /* Text sm/Medium */
                                    font-family: var(--Font-family-font-family-body, Inter);
                                    font-size: var(--Font-size-text-sm, 0.875rem);
                                    font-style: normal;
                                    font-weight: 500;
                                    line-height: var(--Line-height-text-sm, 1.25rem);
                                    /* 142.857% */
                                }

                                .badges-dev {
                                    color: var(--Component-colors-Utility-Success-utility-success-700, #067647);
                                    text-align: center;

                                    /* Text sm/Medium */
                                    font-family: var(--Font-family-font-family-body, Inter);
                                    font-size: var(--Font-size-text-sm, 0.875rem);
                                    font-style: normal;
                                    font-weight: 500;
                                    line-height: var(--Line-height-text-sm, 1.25rem);
                                    /* 142.857% */
                                }

                                .badges-tools {
                                    color: var(--Component-colors-Utility-Pink-utility-pink-700, #C11574);
                                    text-align: center;

                                    /* Text sm/Medium */
                                    font-family: var(--Font-family-font-family-body, Inter);
                                    font-size: var(--Font-size-text-sm, 0.875rem);
                                    font-style: normal;
                                    font-weight: 500;
                                    line-height: var(--Line-height-text-sm, 1.25rem);
                                    /* 142.857% */
                                }
                            }
                        }
                    }
                }
            }

            .blog-arrows {
                display: flex;
                align-items: flex-start;
                gap: var(--spacing-4xl, 2rem);
                padding-left: 22.8rem;

                .blog-left-arrow {
                    display: flex;
                    width: 3.5rem;
                    height: 3.5rem;
                    justify-content: center;
                    align-items: center;
                    gap: var(--spacing-lg, 0.75rem);

                    border-radius: var(--radius-full, 624.9375rem);
                    border: 1px solid var(--Colors-Border-border-secondary, #E4E7EC);

                    &:hover {
                        cursor: pointer;
                    }

                    img {
                        width: 1.5rem;
                        height: 1.5rem;
                        flex-shrink: 0;
                    }
                }

                .blog-right-arrow {
                    @extend .blog-left-arrow;
                }

            }



        }
    }


}