.service-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-7xl, 64px);
    padding: var(--spacing-9xl, 96px) var(--spacing-none, 0px);
    width: 100%;
}

.service-heading,
.service-subheading {
    align-self: stretch;
    position: relative;
    line-height: 1.5rem;
    font-weight: 600;
}

.service-heading {
    margin: 0;
    font-size: var(--display-md-semibold-size);
    letter-spacing: -0.02em;
    line-height: 2.75rem;
    font-family: inherit;
    color: var(--colors-text-text-primary-900);
}

.service-icon{
    width: 44px;
    margin: 12px;
}

.service-heading-and-subheading {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: var(--spacing-lg);
}

.service-supporting-text {
    align-self: stretch;
    position: relative;
    font-size: var(--text-xl-semibold-size);
    line-height: 1.875rem;
    color: var(--colors-text-text-tertiary-600);
}

.service-container-1,
.service-content-header,
.service-heading-and-supporting-text {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}

.service-heading-and-supporting-text {
    width: 48rem;
    align-items: center;
    gap: var(--spacing-2xl);
    max-width: var(--width-xl);
}

.service-container-1,
.service-content-header {
    box-sizing: border-box;
}

.service-content-header {
    align-self: stretch;
    align-items: center;
    padding: 0 var(--padding-xl);
    max-width: 100%;
}

.service-container-1 {
    width: 100%;
    padding: 0 var(--container-padding-desktop);
    max-width: var(--container-max-width-desktop);
    text-align: center;
    color: var(--component-colors-components-buttons-tertiary-color-button-tertiary-color-fg);
    margin-bottom: var(--spacing-7xl);;
}

.service-title,
.supporting-text1 {
    align-self: stretch;
    position: relative;
}

.service-title {
    line-height: 1.875rem;
    font-weight: 600;
    margin-bottom: 12px;
}

.supporting-text1 {
    font-size: 16px;
    line-height: 24px;
    color: var(--colors-text-text-tertiary-600);
}

.text-and-supporting-text {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-start;
    // gap: var(--spacing-md);
}

.text {
    flex: 1;
    position: relative;
    line-height: 1.5rem;
    font-weight: 600;
    white-space: nowrap;
}

.arrow-right-icon {
    height: 1.25rem;
    width: 1.25rem;
    position: relative;
    overflow: hidden;
    flex-shrink: 0;
}

.content2 {
