.map-section {
    display: flex;
    width: 100%;
    padding: var(--spacing-9xl, 96px) var(--spacing-none, 0px);
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-7xl, 64px);
}

.map-section-container {
    display: flex;
    max-width: var(--container-max-width-desktop, 1280px);
    width: 100%;
    padding: var(--spacing-none, 0px) var(--container-padding-desktop, 32px);
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-7xl, 64px);
    box-sizing: border-box;
    overflow: hidden;
}
@media screen and (max-width: 600px) {
    .map-section-container {
        width: 100%;
        overflow: hidden;
    }
    
}

.map-section-header {
    display: flex;
    max-width: var(--width-xl, 768px);
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-2xl, 20px);
    align-self: stretch;
    width: 100%;
    margin: auto;
}

.map-section-header-top {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-xl, 16px);
    align-self: stretch;
}

.map-section-icon {
    display: flex;
    padding: var(--spacing-xs, 4px) var(--spacing-lg, 12px);
    align-items: center;
    border-radius: var(--radius-full, 9999px);
    border: 1.5px solid var(--Component-colors-Utility-Brand-utility-brand-600, #1570EF);
    color: var(--Component-colors-Utility-Brand-utility-brand-700, #175CD3);
    text-align: center;

    /* Text sm/Medium */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-sm, 14px);
    font-style: normal;
    font-weight: 500;
    line-height: var(--Line-height-text-sm, 20px);
    /* 142.857% */
}

.map-section-heading {
    color: var(--colors-text-text-primary-900, #101828);
    text-align: center;

    /* Display md/Semibold */
    font-family: var(--Font-family-font-family-display, Inter);
    font-size: var(--Font-size-display-md, 36px);
    font-style: normal;
    font-weight: 600;
    line-height: var(--Line-height-display-md, 44px);
    /* 122.222% */
    letter-spacing: -0.72px;
}

.map-section-header-description {
    color: var(--colors-text-text-tertiary-600, #475467);
    text-align: center;

    /* Text xl/Regular */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-xl, 20px);
    font-style: normal;
    font-weight: 400;
    line-height: var(--Line-height-text-xl, 30px);
    /* 150% */
}

.map-section-content {
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-7xl, 64px);
}

.map-section-content-container {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    gap: var(--spacing-4xl, 32px);
    align-self: stretch;
    position: relative;
}

.vector-map {
    display: flex;
    width: var(--width-2xl, 1024px);
    padding: 1px 0px 4.013px 0px;
    justify-content: center;
    align-items: center;
    width: 100%;
    position: relative;
}

.pointer {
    position: absolute;
    margin-top: 29px;

}

.innercircle {
    width: 8px;
    height: 8px;
    flex-shrink: 0;
    border-radius: 28px;
    background: var(--colors-foreground-fg-brand-secondary-500, #2E90FA);
    display: flex;
    justify-content: center;
    align-items: center;

}

.middlecircle {
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    border-radius: 28px;
    opacity: 0.2;
    background: var(--colors-foreground-fg-brand-secondary-500, #2E90FA);
}

.outercircle {
    width: 40px;
    height: 40px;
    flex-shrink: 0;
    border-radius: 28px;
    opacity: 0.01;
    background: var(--colors-foreground-fg-brand-secondary-500, #2E90FA);
}

