.section-404 {
    display: flex;
    width: 1440px;
    min-height: 960px;
    padding: var(--spacing-9xl, 96px) var(--spacing-none, 0px);
    // flex-direction: column;
    margin: auto;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-7xl, 64px);
    background: var(--Colors-Background-bg-primary, #FFF);

    .container-404 {
        display: flex;
        max-width: var(--container-max-width-desktop, 1280px);
        padding: var(--spacing-none, 0px) var(--container-padding-desktop, 32px);
        justify-content: center;
        align-items: center;
        gap: var(--spacing-4xl, 32px);
        align-self: stretch;

        .content-404-heading-subheading {
            display: flex;
            padding-right: var(--spacing-4xl, 32px);
            flex-direction: column;
            align-items: flex-start;
            gap: var(--spacing-6xl, 48px);
            flex: 1 0 0;

            .heading-n-supporting-text-404 {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                gap: var(--spacing-3xl, 24px);
                align-self: stretch;

                .heading-n-subheading-404 {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    gap: var(--spacing-lg, 12px);
                    align-self: stretch;

                    .subheading-404 {
                        color: var(--colors-text-text-brand-secondary-700, #175CD3);

                        /* Text md/Semibold */
                        font-family: var(--Font-family-font-family-body, Inter);
                        font-size: var(--Font-size-text-md, 16px);
                        font-style: normal;
                        font-weight: 600;
                        line-height: var(--Line-height-text-md, 24px);
                        /* 150% */
                        align-self: stretch;
                    }

                    .heading-404 {
                        color: var(--colors-text-text-primary-900, #101828);

                        /* Display xl/Semibold */
                        font-family: var(--Font-family-font-family-display, Inter);
                        font-size: var(--Font-size-display-xl, 60px);
                        font-style: normal;
                        font-weight: 600;
                        line-height: var(--Line-height-display-xl, 72px);
                        /* 120% */
                        letter-spacing: -1.2px;
                        align-self: stretch;
                    }
                }

                .supporting-text-404 {
                    color: var(--colors-text-text-tertiary-600, #475467);

                    /* Text xl/Regular */
                    font-family: var(--Font-family-font-family-body, Inter);
                    font-size: var(--Font-size-text-xl, 20px);
                    font-style: normal;
                    font-weight: 400;
                    line-height: var(--Line-height-text-xl, 30px);
                    /* 150% */
                    max-width: var(--width-sm, 480px);
                    align-self: stretch;
                }
            }

            .search-input-404 {
                display: flex;
                max-width: var(--width-sm, 480px);
                align-items: flex-start;
                gap: var(--spacing-xl, 16px);
                align-self: stretch;

                .input-field-404 {
                    display: flex;
                    height: 48px;
                    flex-direction: column;
                    align-items: flex-start;
                    gap: var(--spacing-sm, 6px);
                    flex: 1 0 0;

                    .input-label-404 {
                        display: flex;
                        padding: 10px 14px;
                        align-items: center;
                        gap: var(--spacing-md, 8px);
                        flex: 1 0 0;
                        align-self: stretch;

                        border-radius: var(--radius-md, 8px);
                        border: 1px solid var(--Colors-Border-border-primary, #D0D5DD);
                        background: var(--Colors-Background-bg-primary, #FFF);

                        /* Shadows/shadow-xs */
                        box-shadow: 0px 1px 2px 0px var(--Colors-Effects-Shadows-shadow-xs, rgba(16, 24, 40, 0.05));

                        img {
                            width: 20px;
                            height: 20px;
                        }

                        input {
                            display: -webkit-box;
                            -webkit-box-orient: vertical;
                            -webkit-line-clamp: 1;
                            flex: 1 0 0;
                            border: none;

                            overflow: hidden;
                            color: var(--Colors-Text-text-placeholder, #667085);
                            text-overflow: ellipsis;

                            /* Text md/Regular */
                            font-family: var(--Font-family-font-family-body, Inter);
                            font-size: var(--Font-size-text-md, 16px);
                            font-style: normal;
                            font-weight: 400;
                            line-height: var(--Line-height-text-md, 24px);
                            /* 150% */
                        }
                    }

                    .input-message-404 {
                        @extend .supporting-text-404;
                        display: none;
                    }
                }

                .search-btn-404 {
                    display: flex;
                    padding: var(--spacing-lg, 12px) 18px;
                    justify-content: center;
                    align-items: center;
                    gap: var(--spacing-sm, 6px);
                    height: 48px;

                    border-radius: var(--spacing-md, 8px);
                    border: 2px solid rgba(255, 255, 255, 0.12);
                    background: var(--Component-colors-Components-Buttons-Primary-button-primary-bg, #1570EF);

                    /* Shadows/shadow-xs-skeuomorphic */
                    box-shadow: 0px 0px 0px 1px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner-border, rgba(16, 24, 40, 0.18)) inset, 0px -2px 0px 0px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner, rgba(16, 24, 40, 0.05)) inset, 0px 1px 2px 0px var(--Colors-Effects-Shadows-shadow-xs, rgba(16, 24, 40, 0.05));

                    color: var(--Component-colors-Components-Buttons-Primary-button-primary-fg, #FFF);

                    /* Text md/Semibold */
                    font-family: var(--Font-family-font-family-body, Inter);
                    font-size: var(--Font-size-text-md, 16px);
                    font-style: normal;
                    font-weight: 600;
                    line-height: var(--Line-height-text-md, 24px);
                    /* 150% */
                }
            }

        }

        .content-404-illustration {
            display: flex;
            height: 640px;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            flex: 1 0 0;
        }

        .illustration-404 {
            width: 32.125rem;
            height: 10.25rem;
            flex-shrink: 0;
        }


    }

}