

.contact-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-7xl, 4rem);
    align-self: stretch;
    background: var(--Colors-Background-bg-primary, #FFF);

    .contact-extend {
        display: flex;
        max-width: var(--container-max-width-desktop, 80rem);
        padding: var(--spacing-none, 0rem) var(--container-padding-desktop, 2rem);
        flex-direction: column;
        align-items: flex-start;
        // align-self: stretch;
        box-sizing: border-box;
        width: 100%;
    }

    .contact-section-container-top {
        gap: var(--spacing-2xl, 1.25rem);

        .heading-n-subheading {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            gap: var(--spacing-lg, 0.75rem);
            align-self: stretch;

            .contact-subheading {
                color: var(--colors-text-text-brand-secondary-700, #175CD3);

                /* Text md/Semibold */
                font-family: var(--Font-family-font-family-body, Inter);
                font-size: var(--Font-size-text-md, 1rem);
                font-style: normal;
                font-weight: 600;
                line-height: var(--Line-height-text-md, 1.5rem);
                /* 150% */
                align-self: stretch;
            }

            .contact-heading {
                color: var(--colors-text-text-primary-900, #101828);

                /* Display md/Semibold */
                font-family: var(--Font-family-font-family-display, Inter);
                font-size: var(--Font-size-display-md, 2.25rem);
                font-style: normal;
                font-weight: 600;
                line-height: var(--Line-height-display-md, 2.75rem);
                /* 122.222% */
                letter-spacing: -0.095rem;
                align-self: stretch;
            }
        }

        .contact-supporting-text {
            color: var(--colors-text-text-tertiary-600, #475467);

            /* Text xl/Regular */
            font-family: var(--Font-family-font-family-body, Inter);
            font-size: var(--Font-size-text-xl, 1.25rem);
            font-style: normal;
            font-weight: 400;
            line-height: var(--Line-height-text-xl, 1.875rem);
            /* 150% */
            align-self: stretch;
        }
    }

    .contact-section-container-bottom {
        @extend .contact-extend;
        gap: var(--spacing-7xl, 4rem);

        .contact-section-content {
            display: flex;
            align-items: flex-start;
            gap: 1.5rem var(--spacing-3xl, 1.5rem);
            align-self: stretch;
            flex-wrap: wrap;

            .contact-text {
                display: flex;
                min-width: 17.5rem;
                padding: var(--spacing-3xl, 1.5rem);
                flex-direction: column;
                align-items: flex-start;
                gap: var(--spacing-7xl, 4rem);
                flex: 1 0 0;
                align-self: stretch;
                border-radius: var(--radius-none, 0rem);
                background: var(--Colors-Background-bg-secondary, #F9FAFB);

                .contact-feature-icon {
                    display: flex;
                    width: 3rem;
                    height: 3rem;
                    padding: 0.75rem;
                    justify-content: center;
                    align-items: center;
                    border-radius: var(--radius-lg, 0.625rem);
                    border: 2px solid var(--Gradient-skeuemorphic-gradient-border, rgba(255, 255, 255, 0.12));
                    background: var(--Colors-Background-bg-brand-solid, #1570EF);

                    /* Shadows/shadow-xs-skeuomorphic */
                    box-shadow: 0px 0px 0px 1px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner-border, rgba(16, 24, 40, 0.18)) inset, 0px -2px 0px 0px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner, rgba(16, 24, 40, 0.05)) inset, 0px 1px 2px 0px var(--Colors-Effects-Shadows-shadow-xs, rgba(16, 24, 40, 0.05));

                    img {
                        width: 1.5rem;
                        height: 1.5rem;
                        flex-shrink: 0;
                    }
                }

                .contact-feature-text {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    gap: var(--spacing-2xl, 1.25rem);
                    align-self: stretch;
                    height: 100%;
                    justify-content: space-between;

                    .contact-feature-text-n-supporting-text {
                        display: flex;
                        flex-direction: column;
                        align-items: flex-start;
                        gap: var(--spacing-md, 0.5rem);
                        align-self: stretch;

                        .feature-text {
                            color: var(--colors-text-text-primary-900, #101828);

                            /* Text xl/Semibold */
                            font-family: var(--Font-family-font-family-body, Inter);
                            font-size: var(--Font-size-text-xl, 1.25rem);
                            font-style: normal;
                            font-weight: 600;
                            line-height: var(--Line-height-text-xl, 1.875rem);
                            /* 150% */
                            align-self: stretch;
                        }

                        .feature-supporting-text {
                            color: var(--colors-text-text-tertiary-600, #475467);

                            /* Text md/Regular */
                            font-family: var(--Font-family-font-family-body, Inter);
                            font-size: var(--Font-size-text-md, 1rem);
                            font-style: normal;
                            font-weight: 400;
                            line-height: var(--Line-height-text-md, 1.5rem);
                            /* 150% */
                            align-self: stretch;
                        }

                    }

                    a {
                        text-decoration: none;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        gap: var(--spacing-md, 0.5rem);

                        span {
                            color: var(--Component-colors-Components-Buttons-Tertiary-color-button-tertiary-color-fg, #175CD3);

                            /* Text md/Semibold */
                            font-family: var(--Font-family-font-family-body, Inter);
                            font-size: var(--Font-size-text-md, 1rem);
                            font-style: normal;
                            font-weight: 600;
                            line-height: var(--Line-height-text-md, 1.5rem);
                            /* 150% */
                        }
                    }
                }



            }



        }
    }



}

@media screen and (max-width: 600px){
   .contact-section-container-top.contact-extend{
    padding: 0 16px;
   }
   .contact-section .contact-section-container-bottom{
    padding: 0 16px;
   }
}