.capability-section {
    background-color: var(--colors-background-bg-secondary);
    width: 100rem;
    max-width: 100%;
    padding: var(--spacing-7xl) var(--padding-61xl) var(--spacing-9xl);
    line-height: normal;
    letter-spacing: normal;
    font-family: "Inter", sans-serif;
    margin: 0 auto;
}

.cap-subheading {
    align-self: stretch;
    position: relative;
    line-height: 1.5rem;
    font-weight: 600;
}

.cap-heading {
    align-self: stretch;
    position: relative;
    font-size: 2.25rem;
    letter-spacing: -0.02em;
    line-height: 2.75rem;
    font-weight: 600;
    color: #101828;
}

.cap-heading-and-subheading {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 0.75rem;
}

.cap-supporting-text {
    align-self: stretch;
    position: relative;
    font-size: 1.25rem;
    line-height: 1.875rem;
    color: #475467;
}

.cap-heading-and-supporting-text {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 1.25rem;
    // max-width: 48rem;
}

.content-cab {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.capa-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 0rem 2rem;
    box-sizing: border-box;
    max-width: 80rem;
}

.capability-text {
    align-self: stretch;
    position: relative;
    line-height: 1.875rem;
    font-weight: 600;
}

.capability-suporting-text {
    color: var(--colors-text-text-tertiary-600, #475467);

    /* Text md/Regular */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-md, 16px);
    font-style: normal;
    font-weight: 400;
    line-height: var(--Line-height-text-md, 24px); /* 150% */
    align-self: stretch;
}

.capability-text-and-supporting-text {
    align-self: stretch;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 0.5rem;
}

.learn-txt {
    position: relative;
    line-height: 1.5rem;
    font-weight: 600;
}

.arrow-right-icon {
    width: 1.25rem;
    position: relative;
    height: 1.25rem;
    overflow: hidden;
    flex-shrink: 0;
}

.learn-button {
    overflow: hidden;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    font-size: 1rem;
    color: #175cd3;
}

.capability-service-tab {
    align-self: stretch;
    border-left: 4px solid #2e90fa;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 1rem 0rem 1rem 1.5rem;
    gap: 1.25rem;

    &:hover {
        cursor: pointer;
        background-color: #f9fafb;
      }
}

.capability-service-tab-2 {
    align-self: stretch;
    border-left: 4px solid #f2f4f7;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    padding: 1rem 0rem 1rem 1.5rem;
    gap: 1.25rem;

    &:hover {
        cursor: pointer;
        background-color: #f9fafb;
      }
}

.content-cab {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    max-width: 768px;
}

.space-time-1-icon {
    width: 37rem;
    position: relative;
    height: 41rem;
    object-fit: cover;
}

.capa-container-2 {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    padding: 0rem 2rem;
    box-sizing: border-box;
    gap: 4rem;
    max-width: 80rem;
    font-size: 1.25rem;
    color: #101828;
}

.capability-section {
    width: 100%;
    position: relative;
    background-color: #fff;
    /* overflow: hidden; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 96px 0 0 0;
    box-sizing: border-box;
    gap: 4rem;
    text-align: left;
    font-size: 1rem;
    color: #175cd3;
    font-family: Inter;
}

@media screen and (max-width: 600px){
   
    .capa-container-2{
        flex-wrap: wrap;
        padding: 0 16px;
    }
    .capa-container{
        padding: 0 16px;  
    }

    .plotly-graph-div{
        display: none;
    }
    
}

@media screen and (max-width: 1024px){
    .plotly-graph-div{
        display: none;
    }
    
}