/* 
cta section */

.cta-section {
  display: flex;
  width: 100%;
  padding: var(--spacing-9xl, 96px) var(--spacing-none, 0px);
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-7xl, 64px);
}

/* External CSS file */
.cta-container {
  display: flex;
  max-width: var(--container-max-width-desktop, 1280px);
  padding: var(--spacing-none, 0px) var(--container-padding-desktop, 32px);
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-4xl, 32px);
  align-self: stretch;
  width: 100%;
  margin: 0 auto;
}

.cta-content {
  display: flex;
  padding: var(--spacing-7xl, 64px);
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-4xl, 32px);
  align-self: stretch;
  border-radius: var(--radius-2xl, 16px);
  background: var(--Colors-Background-bg-secondary, #f9fafb);
}

.cta-box {
  display: flex;
  max-width: var(--width-xl, 768px);
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-2xl, 20px);
  align-self: stretch;
  width: 100%;
  margin: auto;
}

.cta-header {
  align-self: stretch;
  height: 124px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  display: flex;
}

.cta-heading {
  color: var(--colors-text-text-primary-900, #101828);
  text-align: center;

  /* Display md/Semibold */
  font-family: var(--Font-family-font-family-display, Inter);
  font-size: var(--Font-size-display-md, 36px);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-display-md, 44px);
