  animation-delay: 0.3s;
}

.progress5 {
  height: 100%;
  background-color: #fefdfd;
  border-radius: 4px;
  animation: progress-animation 4s infinite ease-in-out;
  animation-delay: 2s;
}

.progress6 {
  height: 100%;
  background-color: #fefdfd;
  border-radius: 4px;
  animation: progress-animation 4s infinite ease-in-out;
  animation-delay: 3s;
}

.progress7 {
  height: 100%;
  background-color: #fefdfd;
  border-radius: 4px;
  animation: progress-animation 7s infinite;
  animation-delay: 0.2s;
}

.progress8 {
  height: 100%;
  background-color: #fefdfd;
  border-radius: 4px;
