  animation-delay: 0.6s;
}


@keyframes progress-animation {
  0% {
    width: 10%;
  }

  25% {
    width: 50%;
  }

  50% {
    width: 80%;
  }

  75% {
    width: 50%;
  }

  100% {
    width: 10%;
  }

}

.label {
  margin-right: 10px;
  display: inline-block;
  width: 20px;
}

.parallel-label {
  font-size: 16px;
  letter-spacing: 2px;
  margin-bottom: 10px;
}



.primary-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-7xl, 64px);
  padding: var(--spacing-9xl, 96px) var(--spacing-none, 0px);
  width: 100%;
  // padding-top: var(--spacing-9xl);
 
}
.top-of-website{
  padding-top: 176px;
}

.bg{
  background-color: var(--colors-background-bg-secondary);
}


.secondary-container{
  display: flex;
  flex-wrap: wrap;
  padding: 0 var(--container-padding-desktop);
  max-width: var(--container-max-width-desktop);
  width: 100%;
  align-items: center;
  justify-content: space-between;
  gap: var(--spacing-7xl);
  font-size: var(--text-sm-medium-size);
  // color: var(--component-colors-components-buttons-tertiary-color-button-tertiary-color-fg);
