.hero-content {
  display: flex;
flex-direction: column;
align-items: flex-start;
gap: var(--spacing-6xl, 48px);
flex: 1 0 0;
max-width: 500px;
}

.hero-heading-and-supporting-text {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-3xl);
}



.welcome-badge-text {
  position: relative;
  line-height: 1.25rem;
  font-weight: 500;
  display: inline-block;
  min-width: 5.313rem;
}

.welcome-badge {
  border-radius: var(--radius-full);
  background-color: var(--colors-background-bg-primary);
  border: 1px solid var(--component-colors-utility-brand-utility-brand-200);
  display: flex;
  align-items: center;
  padding: 0 var(--padding-4xs);
  mix-blend-mode: normal;
  white-space: nowrap;
}

.welcome-text-message {
  position: relative;
  line-height: 1.25rem;
  font-weight: 500;
}

.arrow-right-icon {
  height: 1rem;
  width: 1rem;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}

.block {
  position: relative;
  width: 320px;
  height: 34px;
  background: transparent; // Remove the background from the block itself
  border-radius: 34px;
  z-index: 1; // Set a higher z-index to ensure it is above the pseudo-elements
}

.block::before {
  position: absolute;
  border-radius: 34px; // Match the block's border radius
  content: '';
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #000; // Move the background here
  z-index: 0; // Background layer
}

.block::after {
  position: absolute;
  border-radius: 34px;
  content: '';
  left: -1px;
  top: -1px;
  background: linear-gradient(45deg, #fb0094, #0000ff, #00ff00, #ffff00, #ff0000, #fb0094, 
    #0000ff, #00ff00, #ffff00, #ff0000);
  background-size: 200%;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  z-index: -1; // Place it behind the solid background
  animation: steam 20s linear infinite;
  filter: blur(50px);
}

@keyframes steam {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}


.welcome-badge-group:hover {
  cursor: pointer;
}

.welcome-badge-text-secondary {
  display: flex;
  gap: var(--spacing-xs);
}

.welcome-badge-group {
  display: flex;
  border-radius: var(--radius-full);
  background-color: var(--component-colors-utility-brand-utility-brand-50);
  border: 1px solid var(--component-colors-utility-brand-utility-brand-200);
