.full-width-header-navigation {
  display: flex;
  width: 100%;
  height: 80px;
  flex-direction: column;
}

.header {
  margin: auto;
  display: flex;
  width: 100%;
  height: 80px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  /* background: #ffffff; */
  background: var(--Component-colors-Alpha-alpha-white-30, rgba(255, 255, 255, 0.30));
  z-index: 1000;
  backdrop-filter: blur(12px);
  font-family: var(--inter);

}

.header-container {
  margin: auto;
  display: flex;
  width: 100%;
  max-width: var(--container-max-width-desktop, 1280px);
  padding: var(--spacing-none, 0px) var(--container-padding-desktop, 32px);
  justify-content: space-between;
  align-items: center;
 

}

.header-content {
  display: flex;
  align-items: center;
  height: 100%;
  gap: var(--spacing-5xl, 40px);
  flex: 1;
}

.logo {
  display: flex;
  align-items: flex-start;
  margin-right: 36px;

  &:hover {
    cursor: pointer;
  }
}

.logo-wrap {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  gap: 10px;
  
}

.logo-wrap img {
  width: 32px;
  height: 100%;
  object-fit: cover;
}

.logomark {
  display: flex;
  align-items: flex-start;
  width: 32px;
  height: 100%;
  box-shadow: 0px 1px 3px 0px rgba(16, 24, 40, 0.1),
    0px 1px 2px 0px rgba(16, 24, 40, 0.06);
}

.logotext {
  color: #101828;
  display: flex;
  align-items: center;
  justify-content: center;
  // height: 100%;
  flex-shrink: 0;
  width: 142px;
}

.logotext img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.navigation {
  display: flex;
  align-items: center;
  gap: var(--spacing-4xl);
  color: #475467;
  list-style-type: none;
}

.navigation button {
  appearance: none;
  border: none;
  background-color: transparent;
  color: #475467;

  /* Text md/Semibold */
  font-family: var(--Font-family-font-family-body, Inter);
  font-size: var(--Font-size-text-md, 16px);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-text-md, 24px);
  /* 150% */
}

.contact {
  display: flex;
  align-items: center;
  gap: var(--spacing-lg);
  height: 100%;
}

.btn {
  display: flex;
  padding: 10px var(--spacing-xl);
  justify-content: center;
  align-items: center;
  gap: var(--spacing-sm, 6px);
  appearance: none;
  border: none;
  background-color: transparent;
  border: 2px solid rgba(255, 255, 255, 0.12);
}

.btn-contact {
  display: flex;
  padding: 10px var(--spacing-xl, 16px);
  justify-content: center;
  font-weight: 400;
  font-size: 16px;
  align-items: center;
  gap: var(--spacing-sm, 6px);
  border-radius: var(--radius-md);
  color: #ffffff;
  border: none;
  background: var(--Component-colors-Components-Buttons-Primary-button-primary-bg);
  box-shadow: 0px 0px 0px 1px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner-border,
      rgba(16, 24, 40, 0.18)) inset,
    0px -2px 0px 0px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner,
      rgba(16, 24, 40, 0.05)) inset,
    0px 1px 2px 0px var(--Colors-Effects-Shadows-shadow-xs, rgba(16, 24, 40, 0.05));
}



/* Dropdown Content for Resource */
.dropdown-content-resource {
  min-width: max-content;
  display: none;
  position: absolute;
  width: 100vw;
  left: 0;
  top: 80px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  max-height: 0;
  /* Initial max height */
  opacity: 0;
  /* Initial opacity */
  overflow: hidden;
  /* Hide content when not visible */
  z-index: 1000;
  background: white;
}

/* Dropdown Content for Application */
.dropdown-content-application {
  display: none;
  position: absolute;
  width: 336px;
  flex-direction: column;
  align-items: flex-start;
  right: -50%;
  top: 50px;
  border-radius: var(--radius-xl, 12px);
  border: 1px solid var(--Colors-Border-border-secondary, #e4e7ec);
  background: var(--Colors-Background-bg-primary, #fff);
  box-shadow: 0px 12px 16px -4px var(--Colors-Effects-Shadows-shadow-lg_01, rgba(16, 24, 40, 0.08)),
    0px 4px 6px -2px var(--Colors-Effects-Shadows-shadow-lg_02, rgba(16, 24, 40, 0.03));
  opacity: 0;
  /* Initial opacity */
  z-index: 1000;
}

/* Show Dropdown with Animation */
.dropdown-content-resource.show,
.dropdown-content-application.show {
  display: flex;
  max-height: 500px;
  /* Adjust max height as needed */
  opacity: 1;
  /* Full opacity */
}

.application-btn {
  position: relative;
}

.application-btn:hover .dropdown-content-resource.show {
  display: flex;
}

.dropdown-content-resource.show {
  display: flex;

}

.dropdown-content-application.show {
  display: flex;
}

.dropdown-icon {

  transition: transform 0.4s ease;
  display: inline-block;
}

.rotate {
  transform: rotateX(180deg);
}

.btn-arrow {
  display: flex;
  justify-content: center;
  align-items: center;
}

.application-content {
  display: flex;
  padding: var(--spacing-lg, 12px);
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-xs, 4px);
  align-self: stretch;
}

.ac-item {
  display: flex;
  padding: var(--spacing-lg, 12px);
  align-items: flex-start;
  gap: var(--spacing-xl, 16px);
  align-self: stretch;
  border-radius: var(--radius-md, 8px);
  
}

.ac-item:hover {
  background-color:  #f6f6f6; /* Add your desired hover background color */
  cursor: pointer; /* Change cursor to hand */
}

.items-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-family: var(--Font-family-font-family-display, Inter);
  flex: 1 0 0;
}

.item-content-heading {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  color: var(--colors-text-text-primary-900, #101828);
  text-overflow: ellipsis;

  /* Text md/Semibold */
  font-size: var(--Font-size-text-md, 16px);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-text-md, 24px);
  /* 150% */
}

.items-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.down-arrow-icon {
  margin-top: 5px;
  margin-left: 4px;
  width: 20px;
  height: 20px;
}

.item-content-description {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  align-self: stretch;
  overflow: hidden;
  color: var(--colors-text-text-tertiary-600, #475467);
  text-overflow: ellipsis;

  /* Text sm/Regular */
  font-size: var(--Font-size-text-sm, 14px);
  font-style: normal;
  font-weight: 400;
  line-height: var(--Line-height-text-sm, 20px);
  /* 142.857% */
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-shrink: 1;
}

.hamburger-menu {
  display: none;
}

.mobile-content {
  display: none;
}

@media screen and (max-width: 900px) {
  .mobile-container {
    display: flex;
    flex-direction: column;
    position: relative;
  }

  .mobile-content {
    display: none;
  }

  .mobile-content.open {
    display: flex;
    position: fixed;
    z-index: 1000;
    top: 72px;
    width: 100%;
    height: 100vh;

  }


  .header {
    display: flex;
    width: 100%;
    height: 72px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
  }

  .nav-container {
    display: none;
  }

  .hamburger-menu {
    display: flex;
    padding: var(--spacing-md, 8px);
    justify-content: center;
    align-items: center;
    gap: var(--spacing-md, 8px);
  }

  .contact {
    display: none;
  }

  .header-container {
    margin: 0;
    padding: 0;
    width: 100%;
    margin: auto;
  }

  .header-content {
    display: flex;
    flex-direction: column;
    padding: var(--spacing-none, 0px) var(--spacing-lg, 12px) var(--spacing-none, 0px) var(--container-padding-mobile, 16px);
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
    height: auto;
  }

  .hide {
    display: none;
  }

  .nav-container-open {
    display: none;
  }

  .nav-container-open.open {
    display: flex;
    align-items: flex-start;
    width: 100%;
    background: white;
    height: 100%;
    flex-direction: column;
    overflow-y: scroll;
  }

  .header-container-mobile {
    display: flex;
    padding: var(--spacing-none, 0px) var(--spacing-lg, 12px) var(--spacing-none, 0px) var(--container-padding-mobile, 0px);
    justify-content: space-between;
    align-items: center;
    align-self: stretch;
  }

  .navigation {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    padding: 24px 0;
    gap: 16px;
    margin: 0;
    background: #ffffff;
    z-index: 10;


  }

  .navigation li {
    width: 100%;
    padding: 12px 16px;
  }

  .dropdown-menu-footer-navigation {
    display: flex;
    align-items: center;
    gap: var(--spacing-lg, 12px);
    align-self: stretch;
    padding-bottom: 100px;
  }

  .dropdown-menu-footer-navigation-btn-login {
    display: flex;
    padding: 10px var(--spacing-xl, 16px);
    justify-content: center;
    align-items: center;
    gap: var(--spacing-sm, 6px);
    border-radius: var(--radius-md, 8px);
    color: var(--Component-colors-Components-Buttons-Tertiary-button-tertiary-fg, #475467);

    /* Text md/Semibold */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-md, 16px);
    font-style: normal;
    font-weight: 600;
    line-height: var(--Line-height-text-md, 24px);
    /* 150% */
  }

  .dropdown-menu-footer-navigation-btn-signup {
    display: flex;
    width: 100%;
    padding: 10px var(--spacing-xl, 16px);
    justify-content: center;
    align-items: center;
    gap: var(--spacing-sm, 6px);
    border-radius: var(--radius-md, 8px);
    border: 2px solid rgba(255, 255, 255, 0.12);
    background: var(--Component-colors-Components-Buttons-Primary-button-primary-bg, #1570EF);

    /* Shadows/shadow-xs-skeuomorphic */
    box-shadow: 0px 0px 0px 1px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner-border, rgba(16, 24, 40, 0.18)) inset, 0px -2px 0px 0px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner, rgba(16, 24, 40, 0.05)) inset, 0px 1px 2px 0px var(--Colors-Effects-Shadows-shadow-xs, rgba(16, 24, 40, 0.05));
    color: var(--Component-colors-Components-Buttons-Primary-button-primary-fg, #FFF);

    /* Text md/Semibold */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-md, 16px);
    font-style: normal;
    font-weight: 600;
    line-height: var(--Line-height-text-md, 24px);
    /* 150% */
  }

  .btn-arrow {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  .menu {
    width: 100%;
    margin: 0 !important;
    background: #000;
  }

  .navigation li {
    overflow: hidden;
    color: var(--colors-text-text-primary-900, #101828);
    text-overflow: ellipsis;

    /* Text md/Semibold */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-md, 16px);
    font-style: normal;
    font-weight: 600;
    line-height: var(--Line-height-text-md, 24px);
    /* 150% */
  }

  .menu-container {
    display: flex;
    flex-direction: column;
  }

  .menu-content {
    display: flex;
    flex-direction: column;
    padding: 0 !important;
  }

  .down-arrow-icon {
    margin: 0;
  }

  .dropdown-menu-footer {
    display: flex;
    padding: var(--spacing-3xl, 24px) var(--container-padding-mobile, 16px);
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-4xl, 32px);
    align-self: stretch;
    border-top: 1px solid var(--Colors-Border-border-secondary, #E4E7EC);
  }

  .dropdown-menu-footer-content-section {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-lg, 12px);
    flex: 1 0 0;
  }

  .dropdown-menu-footer-content {
    display: flex;
    align-items: flex-start;
    gap: var(--spacing-3xl, 24px);
    align-self: stretch;
  }

  .dropdown-menu-footer-content-section-text {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-md, 8px);
    color: var(--Component-colors-Components-Buttons-Tertiary-button-tertiary-fg, #475467);

    /* Text md/Semibold */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-md, 16px);
    font-style: normal;
    font-weight: 600;
    line-height: var(--Line-height-text-md, 24px);
    /* 150% */
  }

  .resource-btn {
    display: flex;
    flex-direction: column;
    position: none;
  }

  .application-btn {
    display: flex;
    flex-direction: column;
  }

  .dropdown-content-application {
    position: static;
    height: fit-content;
    width: calc(100% - 32px);
    border: none;
    box-shadow: none;

  }

  .dropdown-content-resource {
    position: static;
    height: fit-content;
    position: static;
    height: fit-content;
    width: calc(100% - 32px);
    border: none;
    box-shadow: none;
  }

  .dropdown-content-resource.show,
  .dropdown-content-application.show {
    display: flex;
    max-height: 100%;
    /* Adjust max height as needed */
    opacity: 1;
    /* Full opacity */
    margin-top: 24px;
    box-sizing: border-box;
    width: 100%;
  }

  .menu-section-title {
    padding: 1px 0px;
  }

  .nav-menu-items {
    padding: 12px 0px !important;
  }



  .application-content {
    padding: 0;
  }

  .ac-item {
    padding: 12px 0;
  }

  .menu-section-list {
    width: 100%;
  }

  .menu-content-card {
    margin-top: 40px;
    width: 100%;
  }

  .supporting-text {
    display: none !important;
  }

}