footer {
  display: flex;
  padding: var(--spacing-7xl, 4rem) var(--spacing-none, 0rem)
    var(--spacing-6xl, 3rem) var(--spacing-none, 0rem);
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-7xl, 4rem);
  align-self: stretch;
  background: var(--Colors-Background-bg-brand-section, #1849a9);

  .footer-container-top {
    display: flex;
    width: 100%;
    // max-width: var($--container-max-width-desktop, 80rem);
    max-width: 1280px;
    padding: var(--spacing-none, 0rem) var(--container-padding-desktop, 2rem);
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-6xl, 3rem);
    // align-self: stretch;

    .footer-content {
      display: flex;
      align-items: flex-start;
      gap: var(--spacing-4xl, 2rem);
      align-self: stretch;

      .logo-n-links {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: var(--spacing-4xl, 2rem);
        flex: 1 0 0;

        .footer-logo-n-supporting-text {
          display: flex;
          max-width: var(--width-xxs, 20rem);
          flex-direction: column;
          align-items: flex-start;
          gap: var(--spacing-4xl, 2rem);
          align-self: stretch;

          .footer-logo {
            display: flex;
            // width: 8.875rem;
            // height: 32px;
            justify-content: center;
            align-items: flex-start;
            gap: 0.625rem;
            flex-shrink: 0;

            &:hover {
              cursor: pointer;
            }
          }

          .footer-logo img{
            height: 32px;
          }

          .footer-supporting-text {
            align-self: stretch;
            color: var(--Colors-Text-text-tertiary_on-brand, #b2ddff);

            /* Text md/Regular */
            font-family: var(--Font-family-font-family-body, Inter);
            font-size: var(--Font-size-text-md, 1rem);
            font-style: normal;
            font-weight: 400;
            line-height: var(--Line-height-text-md, 1.5rem);
            /* 150% */
          }
        }

        .footer-links {
          display: flex;
          align-items: center;
          gap: var(--spacing-4xl, 2rem);
          align-self: stretch;

          .footer-link-items {
            text-decoration: none;
            display: flex;
            align-items: center;
            gap: var(--spacing-md, 0.5rem);
            justify-content: center;

            span {
              color: var(
                --Component-colors-Components-Footers-footer-button-fg,
                #b2ddff
              );

              /* Text md/Semibold */
              font-family: var(--Font-family-font-family-body, Inter);
              font-size: var(--Font-size-text-md, 1rem);
              font-style: normal;
              font-weight: 600;
              line-height: var(--Line-height-text-md, 1.5rem);
              /* 150% */
            }
          }
        }
      }

      .footer-app-store-actions {
        // display: flex;
        display: none;
        flex-direction: column;
        align-items: flex-start;
        gap: var(--spacing-xl, 1rem);

        .get-app {
          align-self: stretch;
          color: var(--Colors-Text-text-primary_on-brand, #fff);

          /* Text md/Medium */
          font-family: var(--Font-family-font-family-body, Inter);
          font-size: var(--Font-size-text-md, 1rem);
          font-style: normal;
          font-weight: 500;
          line-height: var(--Line-height-text-md, 1.5rem);

          /* 150% */
          &:hover {
            cursor: pointer;
          }
        }

        .app-store-actions {
          @extend .footer-app-store-actions;

          img {
            height: 2.5rem;
            align-self: stretch;
            stroke-width: 1px;
            stroke: var(--colors-foreground-fg-primary-900, #fff);

            &:hover {
              cursor: pointer;
            }
          }
        }
      }
    }
  }

  .footer-container-bottom {
    display: flex;
    width: 100%;
    max-width: 1280px;
    padding: var(--spacing-none, 0rem) var(--container-padding-desktop, 2rem);
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-4xl, 2rem);
    // align-self: stretch;

    .footer-bottom-content {
      display: flex;
      padding-top: var(--spacing-4xl, 2rem);
      justify-content: space-between;
      align-items: center;
      align-content: center;
      row-gap: var(--spacing-3xl, 1.5rem);
      align-self: stretch;
      flex-wrap: wrap;
      border-top: 1px solid var(--Colors-Border-border-brand_alt, #1570ef);

      .footer-designation {
        color: var(--Colors-Text-text-quaternary_on-brand, #84caff);

        /* Text md/Regular */
        font-family: var(--Font-family-font-family-body, Inter);
        font-size: var(--Font-size-text-md, 1rem);
        font-style: normal;
        font-weight: 400;
        line-height: var(--Line-height-text-md, 1.5rem);
        /* 150% */
      }

      .footer-social-icons {
        display: flex;
        align-items: center;
        gap: var(--spacing-3xl, 1.5rem);

        a {
          text-decoration: none;

          img {
            width: 1.5rem;
            height: 1.5rem;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  footer {
    .footer-container-top {
      .footer-content {
        .footer-links {
          flex-direction: column;
          align-items: flex-start !important;
          justify-content: flex-start;
          height: 100px;
          flex-wrap: wrap;
          gap: 12px !important;
        }
        .footer-app-store-actions {
          display: none;
        }
      }
    }
  }
}
