/* collab-section*/

.collab-container {
    display: flex;
    max-width: var(--container-max-width-desktop, 1280px);
    padding: var(--spacing-none, 0px) var(--container-padding-desktop, 32px);
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-4xl, 32px);
    flex: 1 0 0;
}

.collab-heading {
    color: var(--colors-text-text-tertiary-600, #475467);
    text-align: center;

    /* Text md/Medium */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-md, 16px);
    font-style: normal;
    font-weight: 500;
    line-height: var(--Line-height-text-md, 24px);
    /* 150% */
}

.collab-companies {
    display: flex;
    align-items: flex-start;
    align-content: flex-start;
    gap: 20px ;
    align-self: stretch;
    flex-wrap: wrap;
}

.company-logo-container {
    display: flex;
    align-items: flex-start;
    gap: 6px;
}

.company-logo-container .company-logo-child1 {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
}

.company-logo-container .company-logo-child2 {
    display: flex;
    align-self: center;
    font-size: 28px;
    font-weight: 800;

}



@media screen and (max-width: 600px){
    .collab-companies{
        flex-direction: column;       
    }
}