* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}



:root {
  /* fonts */
  --text-md-medium: Inter;

  /* font sizes */
  --text-md-medium-size: 1rem;
  --text-xl-semibold-size: 1.25rem;
  --display-md-semibold-size: 2.25rem;
  --text-lg-semibold-size: 1.125rem;
  --display-xl-semibold-size: 3.75rem;
  --text-sm-medium-size: 0.875rem;

  /* Colors */
  --colors-background-bg-secondary: #f9fafb;
  --colors-text-text-tertiary-600: #475467;
  --component-colors-components-buttons-secondary-button-secondary-fg: #344054;
  --colors-background-bg-primary: #fff;
  --colors-border-border-secondary: #e4e7ec;
  --component-colors-components-buttons-primary-button-primary-bg: #1570ef;
  --component-colors-components-buttons-tertiary-color-button-tertiary-color-fg: #175cd3;
  --colors-text-text-primary-900: #101828;
  --component-colors-components-buttons-secondary-button-secondary-border: #d0d5dd;
  --component-colors-utility-brand-utility-brand-50: #eff8ff;
  --component-colors-utility-brand-utility-brand-200: #b2ddff;

  /* Spacing */
  --spacing-9xl: 6rem;
  --spacing-4xl: 2rem;
  --container-padding-desktop: 2rem;
  --container-max-width-desktop: 1280px;
  --spacing-3xl: 1.5rem;
  --spacing-7xl: 4rem;
  --spacing-xl: 1rem;
  --spacing-xs: 0.25rem;
  --spacing-lg: 0.75rem;
  --spacing-2xl: 1.25rem;
  --spacing-md: 0.5rem;
  --width-xl: 48rem;
  --spacing-6xl: 3rem;
  --radius-lg: 0.625rem;
  --spacing-xxs: 0.125rem;
  --radius-full: 624.937rem;

  /* Gaps */
  --gap-13xl: 2rem;
  --gap-base: 1rem;
  --gap-5xl: 1.5rem;
  --gap-3xs: 0.625rem;
  --gap-5xs: 0.5rem;
  --gap-45xl: 4rem;
  --gap-xl: 1.25rem;
  --gap-xs: 0.75rem;

  /* Paddings */
  --padding-77xl: 6rem;
  --padding-61xl: 5rem;
  --padding-21xl: 2.5rem;
  --padding-13xl: 2rem;
  --padding-xl: 1.25rem;
  --padding-5xl: 1.5rem;
  --padding-33xl: 3.25rem;
  --padding-11xs: 0.125rem;
  --padding-10xs: 0.187rem;
  --padding-4xs: 0.562rem;

  /* Border radiuses */
  --br-base: 16px;
  --br-xs: 12px;
  --br-3xs: 10px;
  --br-9980xl: 9999px;

  /* Effects */
  --shadows-shadow-xs: 0px 1px 2px rgba(16, 24, 40, 0.05);
  --shadows-shadow-xs-skeuomorphic: 0px 0px 0px 1px rgba(16, 24, 40, 0.18) inset,
    0px -2px 0px rgba(16, 24, 40, 0.05) inset,
    0px 1px 2px rgba(16, 24, 40, 0.05);
}


@mixin displayCenter {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin displayColumn {
  display: flex;
  flex-direction: column;
}

@mixin alignCenter {
  display: flex;
  align-items: center;
}

button:hover {
  cursor: pointer;
}
a{
  text-decoration: none;
  &:hover {
    cursor: pointer;
  }
}

.hover-button:hover {
  background: #0f52ae;

}

.hover-button-white:hover {
  background: #f7f7f7;
}

$--container-max-width-desktop: 1200px; // Example value