anchor-box {
  position: relative;
  width: 100%;
}

sidebar {
  position: absolute;
  left: 110%;
  padding: 32px;
  width: 352px;
  border-radius: var(--radius-2xl, 16px);
  border: 1px solid var(--Colors-Border-border-secondary, #e4e7ec);
  background: #f9fafb;
  box-shadow: 0px 1px 2px 0px
    var(--Colors-Effects-Shadows-shadow-xs, rgba(16, 24, 40, 0.05));
}

.post-page-main-content sidebar p {
  color: #475467;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  margin-bottom: 0px;
}

.post-page-main-content sidebar h2 {
  color: var(--colors-text-text-primary-900, #101828);

  /* Display xs/Semibold */
  font-family: var(--Font-family-font-family-display, Inter);
  font-size: var(--Font-size-display-xs, 24px);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-display-xs, 32px); /* 133.333% */
  margin: 0px;
  padding: 0px 0px 16px 0px;
}

.post-page-main-content sidebar-img img {
  // all: unset;  /* Reset all inherited properties */
  width: 100%;
  margin-bottom: 32px;
  border-radius: 16px;
  margin-top: 0px;
}

sidebar-img {
  position: absolute;
  left: 110%;
  width: 352px;
  border-radius: var(--radius-2xl, 16px);
  border: 1px solid var(--Colors-Border-border-secondary, #e4e7ec);
  box-shadow: 0px 1px 2px 0px
    var(--Colors-Effects-Shadows-shadow-xs, rgba(16, 24, 40, 0.05));
}

.post-page-main-content sidebar-img p {
  color: #475467;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  margin-bottom: 0px;
  padding: 16px;
}