/* Container Styles */

.feature-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-7xl, 64px);
    padding: var(--spacing-9xl, 96px) var(--spacing-none, 0px);
    width: 100%;
  }
  .feature-container {
    width: 100%;
    max-width: 1280px;
    height: 100%;
    background: white;
    display: inline-flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 64px;
  }
  
  /* Header Section Styles */
  .feature-header-section {
    align-self: stretch;
    padding-left: 32px;
    padding-right: 32px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 32px;
  }
  
  .feature-header-content {
    display: flex;
    max-width: var(--width-xl, 768px);
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-2xl, 20px);
    align-self: stretch;
  }
  
  .feature-header-title {
    align-self: stretch;
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 12px;
  }
  
  .feature-header-title-primary {
    color: #175cd3;
    font-size: 16px;
    font-family: Inter;
    font-weight: 600;
    line-height: 24px;
    word-wrap: break-word;
  }
  
  .feature-header-title-secondary {
    color: var(--colors-text-text-primary-900, #101828);
    align-self: stretch;
    font-family: var(--Font-family-font-family-display, Inter);
    font-size: var(--Font-size-display-md, 36px);
    font-style: normal;
    font-weight: 600;
    line-height: var(--Line-height-display-md, 44px); /* 122.222% */
    letter-spacing: -0.72px;
  }
  
  .feature-header-description {
    align-self: stretch;
    color: #475467;
    font-size: 20px;
    font-family: Inter;
    font-weight: 400;
    line-height: 30px;
    word-wrap: break-word;
  }
  
  /* Capabilities Section Styles */
  .feature-capabilities-section {
    align-self: stretch;
    height: 386px;
    padding-left: 32px;
    padding-right: 32px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 64px;
  }
  
  .feature-capability-cards {
    display: flex;
    max-width: var(--container-max-width-desktop, 1280px);
    padding: var(--spacing-none, 0px) var(--container-padding-desktop, 32px);
  
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-7xl, 64px);
    align-self: stretch;
  }
  
  /* Individual Card Styles */
  .feature-capability-card {
    display: flex;
    min-width: 286px;
    // max-width: 286px;
    padding: var(--spacing-3xl, 24px);
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-7xl, 64px);
    flex: 1 0 0;
    align-self: stretch;
    border-radius: var(--radius-none, 0px);
    background: var(--Colors-Background-bg-secondary, #f9fafb);
  }
  .feature-capability-cards-content {
    display: flex;
    align-items: flex-start;
    gap: 24px var(--spacing-3xl, 24px);
    align-self: stretch;
    flex-wrap: wrap;
  }
  
  .feature-capability-icon {
    width: 48px;
    height: 48px;
    padding: 12px;
    background: #1570ef;
    box-shadow: 0px 1px 2px rgba(16, 24, 40, 0.05);
    border-radius: 10px;
    overflow: hidden;
    border: 2px white solid;
    display: inline-flex;
    justify-content: center;
    align-items: center;
  }
  
  .feature-capability-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-2xl, 20px);
    align-self: stretch;
  }
  
  .feature-capability-title {
    color: #101828;
    font-size: 20px;
    font-family: Inter;
    font-weight: 600;
    line-height: 30px;
    word-wrap: break-word;
  }
  
  .feature-capability-description {
    color: #475467;
    font-size: 16px;
    font-family: Inter;
    font-weight: 400;
    line-height: 24px;
    word-wrap: break-word;
  }
  
  .feature-learn-more {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 8px;
  }
  
  .feature-learn-more-text {
    color: #175cd3;
    font-size: 16px;
    font-family: Inter;
    font-weight: 600;
    line-height: 24px;
    word-wrap: break-word;
  }
  
  .box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-md, 8px);
    align-self: stretch;
  }

  @media screen and (max-width: 600px){


    .feature-section{
      padding-bottom: 0;
    }
    .feature-header-section{
      padding: 0 16px;
    }
    .feature-capability-cards {
      padding: 0 16px;
    }
  }