.slider-container {
    z-index: 100;
    // padding-left: 830px;
}

.slider {
    display: flex;
    overflow-x: scroll;
    scroll-snap-align: center;
    scrollbar-width: none;
    align-items: flex-start;
    gap: var(--spacing-4xl, 32px);
    transition: transform 0.5s ease-in-out;
    z-index: 1;
    max-width: 100vw;
    width: 100%;
    padding-left: calc((100vw - 1216px) / 2);
    padding-right: 22rem;
}

.slider-card {
    display: flex;
    min-width: 384px;
    width: 100%;
    // height: 504px;
    padding: var(--spacing-2xl, 20px);
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    gap: var(--spacing-3xl, 24px);
    border-radius: var(--radius-none, 0px);
    z-index: 1;
}



.slider-card:nth-child(1) {
    background: var(--Colors-Brand-600, #088ab2);
}

.slider-card:nth-child(2) {
    background: var(--Colors-Success-600, #444ce7);
}

.slider-card:nth-child(3) {
    background: var(--Colors-Blue-600, #1570ef);
}

.slider-card:nth-child(4) {
    background: var(--Colors-Indigo-600, #0e9384);
}

.slider-card:nth-child(5) {
    background: var(--Colors-Success-600, #ca8504);
}

.heading-context {
    color: var(--colors-text-text-primary-900, #101828);
    align-self: stretch;
    /* Display md/Semibold */
    font-family: var(--Font-family-font-family-display, Inter);
    font-size: var(--Font-size-display-md, 36px);
    font-style: normal;
    font-weight: 600;
    line-height: var(--Line-height-display-md, 44px);
    /* 122.222% */
    letter-spacing: -0.72px;
}

.prev {
    left: 0;
}

.next {
    right: 0;
}

.testimonial-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: var(--spacing-9xl, 96px) var(--spacing-none, 0px);
    background: transparent;
    width: 100%;
}

.testimonial-container {
    display: flex;
    max-width: var(--container-max-width-desktop, 1280px);
    padding: var(--spacing-none, 0px) var(--container-padding-desktop, 32px);
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-7xl, 64px);
    margin-bottom: 64px;
    box-sizing: border-box;
    max-width: 1280px;
    width: 100%;
}

.testimonial-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    align-content: flex-start;
    row-gap: var(--spacing-4xl, 32px);
    align-self: stretch;
    flex-wrap: wrap;
}

.head-container {
    display: flex;
    /* width: 768px; */
    max-width: var(--width-xl, 100%);
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-2xl, 20px);
    flex: 1 0 0;
}

.see-all-btn {
    display: flex;
    align-items: flex-start;
    gap: var(--spacing-lg, 12px);
}

.see-all-btn button {
    display: flex;
    padding: var(--spacing-lg, 12px) 18px;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-sm, 6px);
    border-radius: var(--radius-md, 8px);
    border: 2px solid rgba(255, 255, 255, 0.12);
    background: var(--Component-colors-Components-Buttons-Primary-button-primary-bg,
            #1570ef);
    color: #ffffff;
    font-size: 16px;

    /* Shadows/shadow-xs-skeuomorphic */
    box-shadow: 0px 0px 0px 1px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner-border,
            rgba(16, 24, 40, 0.18)) inset,
        0px -2px 0px 0px var(--Colors-Effects-Shadows-shadow-skeumorphic-inner,
            rgba(16, 24, 40, 0.05)) inset,
        0px 1px 2px 0px var(--Colors-Effects-Shadows-shadow-xs, rgba(16, 24, 40, 0.05));
}

.testimonial-content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-4xl, 32px);
    margin-bottom: 32px;
}

.first {
    border-radius: var(--radius-none, 0px);
    background: var(--Colors-Brand-600, #1570ef);
}

.second {
    border-radius: var(--radius-none, 0px);
    background: var(--Colors-Success-600, #079455);
}

.third {
    border-radius: var(--radius-none, 0px);
    background: var(--Colors-Blue-600, #1570ef);
}

.fourth {
    border-radius: var(--radius-none, 0px);
    background: var(--Colors-Indigo-600, #444ce7);
}

.fifth {
    border-radius: var(--radius-none, 0px);
    background: var(--Colors-Success-600, #079455);
}

.arrow-btn {
    display: flex;
    align-items: flex-start;
    gap: var(--spacing-4xl, 32px);
}

.arrow-btn button {
    display: flex;
    width: 56px;
    height: 56px;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-lg, 12px);
    border-radius: var(--radius-full, 9999px);
    border: 1px solid var(--Colors-Border-border-secondary, #e4e7ec);
}

.company-text {
    height: 55%;
    display: flex;
    padding: var(--spacing-4xl, 32px) var(--spacing-3xl, 24px);
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-4xl, 32px);
    align-self: stretch;
    border-radius: var(--radius-none, 0px);
    border: 1px solid var(--Component-colors-Alpha-alpha-white-30, rgba(255, 255, 255, 0.30));
    background: var(--Component-colors-Alpha-alpha-white-30, rgba(255, 255, 255, 0.30));

    /* Backdrop blurs/backdrop-blur-lg */
    backdrop-filter: blur(12px);
}

.company-logo {
    display: flex;
    gap: 12px;
    margin-bottom: 130px;
}

.company-quote {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--spacing-xl, 16px);
    align-self: stretch;
}

.company-quote-header {
    align-self: stretch;
    /* Display sm/Semibold */
    color: var(--Colors-Text-text-white, #FFF);
    font-family: var(--Font-family-font-family-display, Inter);
    font-size: var(--Font-size-display-sm, 30px);
    font-style: normal;
    font-weight: 600;
    line-height: var(--Line-height-display-sm, 38px);
    letter-spacing: -0.96px;

    /* 126.667% */
}

.company-quote-text {
    color: var(--Colors-Text-text-white, #FFF);
    align-self: stretch;
    /* Text lg/Medium */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-lg, 18px);
    font-style: normal;
    font-weight: 500;
    line-height: var(--Line-height-text-lg, 28px);
    /* 155.556% */
}

.case-study-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: var(--spacing-md, 8px);
}

.case-text {
    color: var(--Colors-Foreground-fg-white, #FFF);
    /* Text md/Semibold */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-md, 16px);
    font-style: normal;
    font-weight: 600;
    line-height: var(--Line-height-text-md, 24px);
    /* 150% */
}

.case-icon, .case-icon-svg {
    width: 20px;
    height: 20px;
}

.container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.heading-description {
    color: var(--colors-text-text-tertiary-600, #475467);

    /* Text xl/Regular */
    font-family: var(--Font-family-font-family-body, Inter);
    font-size: var(--Font-size-text-xl, 20px);
    font-style: normal;
    font-weight: 400;
    line-height: var(--Line-height-text-xl, 30px);
    /* 150% */
}

.btn-container {
    max-width: 1280px;
    width: 100%;
    padding-left: 45px;
}
@media screen and (max-width: 600px){
    .testimonial-section{
        padding: 96px 0;
       

    }
    .testimonial-container{
        padding: 0 16px;
        flex-wrap: wrap;
        width: 100%;
    }
    .testimonial-content{
        width: 100%;
        flex-wrap: wrap;
    }
    .slider-container{
        width: 100%;
        flex-wrap: wrap;
    }
    .slider{
        width: 100%;
        padding: 0 25px;
        
        
    }
    .testimonial-header{
        flex-direction: column;
       
    }
    .head-container{
        align-items: center;
        
    }
    .btn-container{
        display: flex;
        flex-direction: column;
        padding: 0 16px;
    }
    .arrow-btn{
        justify-content: space-between;
        width: 100%;
    }
    #scene{
        display: none;
    }

}

@media screen and (max-width: 1024px){
 .slider{
    padding:16px;
 }
}


.company-logotext{
    font-size: 30px;
    font-weight: 600;
    color: #fff;
    opacity: 0.84;
    align-self: center;
}

.company-logomark{
    max-width: 52px;
}