.pricing-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-7xl, 64px);
  width: 100%;
  background: #f9fafb;
  margin-top: 32px;
}

/* Container styles */
.pricing-container {
  width: 100%;
  height: 100%;
  max-width: 1280px;
  padding: 96px 0;

  display: inline-flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 64px;
}

.pricing-content-wrapper {
  align-self: stretch;
  padding-left: 32px;
  padding-right: 32px;
  display: inline-flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 64px;
}

.pricing-column {
  display: flex;
  min-width: 360px;
  max-width: 440px;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-2xl, 20px);
  flex: 1 0 0;
}

/* Header styles */
.pricing-header {
  align-self: stretch;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 12px;
}

.pricing-title {
  align-self: stretch;
  color: #175cd3;
  font-size: 16px;
  font-family: Inter;
  font-weight: 600;
  line-height: 24px;
  word-wrap: break-word;
}

.pricing-subtitle {
  color: var(--colors-text-text-primary-900, #101828);
  align-self: stretch;
  /* Display md/Semibold */
  font-family: var(--Font-family-font-family-display, Inter);
  font-size: var(--Font-size-display-md, 36px);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-display-md, 44px); /* 122.222% */
  letter-spacing: -0.72px;
}

.pricing-description {
  align-self: stretch;
  color: #475467;
  font-size: 18px;
  font-family: Inter;
  font-weight: 400;
  line-height: 28px;
  word-wrap: break-word;
}

/* Flex Wrapper styles */
.pricing-flex-wrapper {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  gap: var(--spacing-4xl, 32px);
  flex: 1 0 0;
}

/* Card styles */
.pricing-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex: 1 0 0;
  border-radius: var(--radius-2xl, 16px);
  border: 1px solid var(--Colors-Border-border-secondary, #e4e7ec);
  background: var(--Colors-Background-bg-primary, #fff);

  /* Shadows/shadow-lg */
  box-shadow: 0px 12px 16px -4px var(--Colors-Effects-Shadows-shadow-lg_01, rgba(16, 24, 40, 0.08)),
    0px 4px 6px -2px var(--Colors-Effects-Shadows-shadow-lg_02, rgba(16, 24, 40, 0.03));
}

.pricing-card-header {
  display: flex;
  padding: var(--spacing-4xl, 32px) var(--spacing-4xl, 32px)
    var(--spacing-none, 0px) var(--spacing-4xl, 32px);
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  align-self: stretch;
}

.pricing-card-header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-md, 8px);
  align-self: stretch;
}

.pricing-icon-title-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-xl, 16px);
  align-self: stretch;
}

.pricing-icon-wrapper {
  width: 48px;
  height: 48px;
  padding: 12px;
  background: #d1e9ff;
  border-radius: 9999px;
  overflow: hidden;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.pricing-icon-line,
.pricing-icon-line-2 {
  width: 16.86px;
  height: 20px;
  border: 2px #1570ef solid;
}

.pricing-card-title {
  color: var(--colors-text-text-brand-secondary-700, #175cd3);
  text-align: center;

  /* Text xl/Semibold */
  font-family: var(--Font-family-font-family-body, Inter);
  font-size: var(--Font-size-text-xl, 20px);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-text-xl, 30px); /* 150% */
  align-self: stretch;
}

.pricing-card-subtitle {
  color: var(--colors-text-text-primary-900, #101828);
  text-align: center;

  /* Display lg/Semibold */
  font-family: var(--Font-family-font-family-display, Inter);
  font-size: var(--Font-size-display-lg, 48px);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-display-lg, 60px); /* 125% */
  letter-spacing: -0.96px;
  align-self: stretch;
}

/* Body styles */
.pricing-card-body {
  align-self: stretch;
  height: 344px;
  padding-top: 32px;
  padding-bottom: 40px;
  padding-left: 32px;
  padding-right: 32px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 24px;
}

.pricing-card-item {
  align-self: stretch;
  display: inline-flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 12px;
}

.pricing-item-icon-wrapper {
  width: 24px;
  height: 24px;
  position: relative;
  background: #eff8ff;
  border-radius: 9999px;
  overflow: hidden;
}

.pricing-item-icon {
  width: 12px;
  height: 10.5px;
  left: 6.35px;
  top: 7.12px;
  position: absolute;
  background: #1570ef;
}

.pricing-item-description {
  flex: 1 1 0;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  word-wrap: break-word;
  color: #475467;
  font-size: 16px;
  font-family: Inter;
  font-weight: 400;
  line-height: 24px;
}

/* Footer and button styles */
.pricing-card-footer {
  display: flex;
  padding: var(--spacing-4xl, 32px);
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-3xl, 24px);
  align-self: stretch;
  border-top: 1px solid var(--Colors-Border-border-secondary, #e4e7ec);
  background: var(--Colors-Background-bg-secondary, #f9fafb);
  border-radius: 0 0 16px 16px;
}

.pricing-button-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-lg, 12px);
  align-self: stretch;
}

.pricing-button {
  display: flex;
  padding: var(--spacing-lg, 12px) 18px;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-sm, 6px);
  align-self: stretch;
  border-radius: var(--radius-md, 8px);
  border: 2px solid rgba(255, 255, 255, 0.12);
  background: var(
    --Component-colors-Components-Buttons-Primary-button-primary-bg,
    #1570ef
  );

  /* Shadows/shadow-xs-skeuomorphic */
  box-shadow: 0px 0px 0px 1px
      var(
        --Colors-Effects-Shadows-shadow-skeumorphic-inner-border,
        rgba(16, 24, 40, 0.18)
      )
      inset,
    0px -2px 0px 0px var(
        --Colors-Effects-Shadows-shadow-skeumorphic-inner,
        rgba(16, 24, 40, 0.05)
      ) inset,
    0px 1px 2px 0px
      var(--Colors-Effects-Shadows-shadow-xs, rgba(16, 24, 40, 0.05));
}

.pricing-button-text {
  color: white;
  font-size: 16px;
  font-family: Inter;
  font-weight: 500;
  line-height: 24px;
  word-wrap: break-word;
}

@media screen and (max-width: 1011px) {
  .pricing-content-wrapper {
    flex-direction: column;
    padding: 0 16px;
  }
}

@media screen and (max-width: 600px) {
  .pricing-content-wrapper {
    flex-direction: column;
    padding: 0 16px;
  }

  .pricing-flex-wrapper {
    flex-direction: column;
  }
}
