
.menu-container {
  display: flex;
  max-width: var(--container-max-width-desktop, 80rem);
  align-items: flex-start;
  align-self: stretch;
  background:white;
}

.menu-container .menu-content {
  display: flex;
  padding: var(--spacing-4xl, 2rem);
  align-items: flex-start;
  gap: var(--spacing-3xl, 1.5rem);
  flex: 1 0 0;
  align-self: stretch;
  /* background-color: aqua; */
}

.menu-container .menu-content .menu-content-card {
  display: flex;
  width: 19.75rem;
  padding: var(--spacing-2xl, 1.25rem) var(--spacing-4xl, 2rem)
    var(--spacing-2xl, 1.25rem) var(--spacing-2xl, 1.25rem);
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-xl, 1rem);
  align-self: stretch;
  /* background-color: blueviolet; */
}
.menu-content .menu-section {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-lg, 0.75rem);
  flex: 1 0 0;
  /* background-color: rgb(94, 206, 246); */
}

.menu-content .menu-section .menu-section-title {
  color: var(--colors-text-text-brand-tertiary-600, #1570ef);
  align-self: stretch;
  /* Text sm/Semibold */
  font-family: var(--Font-family-font-family-body, Inter);
  font-size: var(--Font-size-text-sm, 0.875rem);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-text-sm, 1.25rem);
}

.menu-content .menu-section .menu-section-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-xs, 0.25rem);
  align-self: stretch;
}

.menu-content .menu-section .nav-menu-items {
  display: flex;
  padding: var(--spacing-lg, 0.75rem);
  align-items: flex-start;
  gap: var(--spacing-xl, 1rem);
  align-self: stretch;
  /* background-color: #dba5ea; */
}

.menu-content .menu-section .nav-menu-items:hover {
  background-color:  #f6f6f6; /* Add your desired hover background color */
  cursor: pointer; /* Change cursor to hand */
  border-radius: var(--radius-md, 8px);
}

.menu-content .menu-section .nav-menu-items .menu-icons {
  width: 1.5rem;
  height: 1.5rem;
}
.menu-content .menu-section .nav-menu-items .menu-item-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-lg, 0.75rem);
  flex: 1 0 0;
}

.menu-item-content .text-n-supporting-text .text-n-badge {
  overflow: hidden;
  color: var(--colors-text-text-primary-900, #101828);
  text-overflow: ellipsis;

  display: flex;
  align-items: center;
  gap: var(--spacing-md, 0.5rem);
  align-self: stretch;
  box-sizing: border-box;

  /* display: -webkit-box; */
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;

  /* Text md/Semibold */
  font-family: var(--Font-family-font-family-body, Inter);
  font-size: var(--Font-size-text-md, 1rem);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-text-md, 1.5rem); /* 150% */
}
.menu-item-content .text-n-supporting-text .text-n-badge .hiring {
  display: flex;
  padding: var(--spacing-xxs, 0.125rem) 0.625rem;
  align-items: center;

  border-radius: var(--radius-full, 624.9375rem);
  border: 1px solid
    var(--Component-colors-Utility-Success-utility-success-200, #abefc6);
  background: var(
    --Component-colors-Utility-Success-utility-success-50,
    #ecfdf3
  );
}
.hiring span {
  color: var(--Component-colors-Utility-Success-utility-success-700, #067647);
  text-align: center;

  /* Text sm/Medium */
  font-family: var(--Font-family-font-family-body, Inter);
  font-size: var(--Font-size-text-sm, 0.875rem);
  font-style: normal;
  font-weight: 500;
  line-height: var(--Line-height-text-sm, 1.25rem); /* 142.857% */
}

.menu-item-content .text-n-supporting-text .supporting-text {
  overflow: hidden;
  color: var(--colors-text-text-tertiary-600, #475467);
  text-overflow: ellipsis;

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  align-self: stretch;

  /* Text sm/Regular */
  font-family: var(--Font-family-font-family-body, Inter);
  font-size: var(--Font-size-text-sm, 0.875rem);
  font-style: normal;
  font-weight: 400;
  line-height: var(--Line-height-text-sm, 1.25rem); /* 142.857% */
}

.menu-content-card {
  display: flex;
  width: 19.75rem;
  padding: var(--spacing-2xl, 1.25rem) var(--spacing-4xl, 2rem)
    var(--spacing-2xl, 1.25rem) var(--spacing-2xl, 1.25rem);
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-xl, 1rem);
  align-self: stretch;
  background: var(--Colors-Background-bg-secondary, #f9fafb);
  /* background-color: #5f90c1; */
}

.menu-content-card .nav-menu-item-card {
  display: flex;
  /* width: 100%; */
  padding: var(--spacing-lg, 0.75rem);
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-xl, 1rem);
  align-self: stretch;
  border-radius: var(--radius-md, 0.5rem);
  /* background-color: rebeccapurple; no bg */
}
.menu-content-card .nav-menu-item-card .image-wrap {
  /* height: 8.5rem; */
  /* max-width: 15rem; */
  /* width: 100%; */
  width: 100%;
  height: 100%;
  object-fit: contain;


  align-self: stretch;
  display: flex;
  border-radius: var(--radius-sm, 0.375rem);
  box-sizing: border-box;
  border-radius: 8px;
}
.image-wrap .play_button {
  width: 3rem;
  height: 3rem;
  flex-shrink: 0;
  align-self: flex-end;
  backdrop-filter: blur(8px);
}

.menu-content-card .nav-menu-item-card .card-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-lg, 0.75rem);
  align-self: stretch;
}
.menu-content-card .nav-menu-item-card .card-content .text-n-supporting-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--spacing-xs, 0.25rem);
  align-self: stretch;
}
.text-n-supporting-text .card-text {
  overflow: hidden;
  color: var(--colors-text-text-primary-900, #101828);
  text-overflow: ellipsis;

  /* Text md/Semibold */
  font-family: var(--Font-family-font-family-body, Inter);
  font-size: var(--Font-size-text-md, 1rem);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-text-md, 1.5rem); /* 150% */

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  align-self: stretch;
}
.text-n-supporting-text .supporting-text {
  overflow: hidden;
  color: var(--colors-text-text-tertiary-600, #475467);
  text-overflow: ellipsis;

  /* Text sm/Regular */
  font-family: var(--Font-family-font-family-body, Inter);
  font-size: var(--Font-size-text-sm, 0.875rem);
  font-style: normal;
  font-weight: 400;
  line-height: var(--Line-height-text-sm, 1.25rem); /* 142.857% */

  display: -webkit-box;
  -webkit-box-orient: vertical;
  align-self: stretch;
}

.menu-content-card .nav-menu-item-card .card-content .card-actions {
  display: flex;
  align-items: flex-start;
  gap: var(--spacing-lg, 0.75rem);
}
.menu-content-card .nav-menu-item-card .card-content .card-actions button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-sm, 0.375rem);
}
.menu-content-card
  .nav-menu-item-card
  .card-content
  .card-actions
  button:nth-child(1) {
  color: var(
    --Component-colors-Components-Buttons-Tertiary-button-tertiary-fg,
    #475467
  );

  /* Text sm/Semibold */
  font-family: var(--Font-family-font-family-body, Inter);
  font-size: var(--Font-size-text-sm, 0.875rem);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-text-sm, 1.25rem); /* 142.857% */
}
.menu-content-card
  .nav-menu-item-card
  .card-content
  .card-actions
  button:nth-child(2) {
  color: var(
    --Component-colors-Components-Buttons-Tertiary-color-button-tertiary-color-fg,
    #175cd3
  );

  /* Text sm/Semibold */
  font-family: var(--Font-family-font-family-body, Inter);
  font-size: var(--Font-size-text-sm, 0.875rem);
  font-style: normal;
  font-weight: 600;
  line-height: var(--Line-height-text-sm, 1.25rem); /* 142.857% */
}

.menu {
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: stretch;
  justify-content: center;
}